@font-face {
  font-family: "Nunito-Regular";
  src: url("../fonts/Nunito-Regular.woff2") format("woff2"), url("../fonts/Nunito-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Nunito-Light";
  src: url("../fonts/Nunito-Light.woff2") format("woff2"), url("../fonts/Nunito-Light.woff") format("woff");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Nunito-Bold";
  src: url("../fonts/Nunito-Bold.woff2") format("woff2"), url("../fonts/Nunito-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Exo2-ExtraBold";
  src: url("../fonts/Exo2-ExtraBold.woff2") format("woff2"), url("../fonts/Exo2-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Exo2-Bold";
  src: url("../fonts/Exo2-Bold.woff2") format("woff2"), url("../fonts/Exo2-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*, *::before, *::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  font-family: "Nunito-Regular", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
  color: #938B88;
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  margin: 0;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
}

.image-wrapper {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo {
  width: 176px;
}

.heading {
  font-family: "Exo2-ExtraBold", sans-serif;
  font-weight: 800;
  font-style: normal;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: 0.02em;
  color: #722C5B;
}

.subheading {
  font-family: "Nunito-Light", sans-serif;
  font-weight: 200;
  font-size: 36px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #C4C4C4;
  text-transform: uppercase;
}

.text {
  font-family: "Nunito-Light", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 18px;
  line-height: 28px;
  color: #938B88;
}

.button {
  vertical-align: middle;
  background-color: #5EBDE4;
  color: #ffffff;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  outline: none;
  height: 48px;
}

.invalid {
  color: #722C5B;
  font-size: 14px;
  padding-top: 6px;
}

.navbar {
  background-color: #F3FAFF;
}
.navbar__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-top: 22px;
}
.navbar__icon {
  display: none;
}
.navbar__menu {
  margin-bottom: 7px;
}
.navbar__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__item {
  margin-right: 34px;
}
.navbar__item:last-child {
  margin-right: 0;
}
.navbar__item-lang {
  margin-right: 5px;
}
.navbar__item-lang:first-child::after {
  content: "|";
  padding-left: 2px;
  color: #5EBDE4;
}
.navbar__link {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #5EBDE4;
}
.navbar__link:after {
  content: "";
  display: block;
  height: 2px;
  background-color: #5EBDE4;
  width: 0%;
  transition: 0.5s;
}
.navbar__link:hover:after {
  width: 85%;
}
.navbar__link-lang {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #5EBDE4;
}
.navbar__link-active {
  font-weight: 700;
}
.navbar__data {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  min-width: 215px;
  margin-bottom: 7px;
}
.navbar__info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-left: 29px;
}
.navbar__text {
  font-weight: 400;
  font-size: 9px;
  line-height: 10px;
  color: #722C5B;
  letter-spacing: 0.02em;
}
.navbar__phone {
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #722C5B;
}

.promo {
  position: relative;
  background: #F3FAFF url(../images/bg/stars.svg) repeat 0 0;
  padding-top: 57px;
  padding-bottom: 65px;
}
.promo__first {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.promo__title {
  width: 535px;
  margin: 0;
  margin-bottom: 35px;
  font-family: "Exo2-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 58px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: #5EBDE4;
}
.promo__cards {
  position: absolute;
  top: 470px;
  display: flex;
}
.promo__card {
  width: 190px;
  padding: 46px 13px 35px 24px;
  background: url("../images/bg/promo_card.svg") no-repeat top center;
  box-shadow: 10px 18px 20px rgba(94, 189, 228, 0.26);
  border-radius: 10px;
}
.promo__card:not(:last-child) {
  margin-right: 25px;
}
.promo__card-title {
  display: block;
  margin-bottom: 18px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  color: #722C5B;
}
.promo__card-description {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #938B88;
}
.promo__description {
  margin: 0;
  padding-right: 57px;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #5EBDE4;
}
.promo__icon {
  margin: 0 auto;
  margin-bottom: 21px;
  width: 90px;
  height: 90px;
}
.promo__image {
  margin-top: 25px;
  margin-right: 50px;
  width: 600px;
  height: 600px;
  position: relative;
}
.promo__image-wrapper::before {
  content: "";
  position: absolute;
  top: -30px;
  width: 600px;
  height: 600px;
  background-image: url("../images/blobs/promo-wrp.svg");
  animation: promo-wrp 4.5s infinite linear;
}
.promo__image-fon {
  width: 600px;
  height: 600px;
  object-fit: cover;
  animation: fon 2s 1 ease-out;
}
.promo_photo {
  width: 597px;
  height: 597px;
}

.clip {
  clip-path: url(#svgPromo);
}

.about {
  background-color: #ffffff;
  background: url(../images/bg/promo_wave.svg) repeat-x center -3px;
  margin-bottom: 110px;
}
.about__first {
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding-top: 145px;
}
.about__heading {
  margin: 0;
  margin-bottom: 28px;
}
.about__subheading {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #722C5B;
}
.about__text {
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
}
.about__image {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 110px;
  padding-left: 80px;
}
.about__image-wrapper {
  position: relative;
  width: 450px;
  height: 450px;
}
.about__image-wrapper::before {
  content: "";
  position: absolute;
  width: 450px;
  height: 450px;
  background: url("../images/blobs/about-wrp.svg") no-repeat;
  animation: about-wrp 4.5s infinite linear;
}
.about__image-fon {
  width: 450px;
  height: 450px;
  object-fit: cover;
  animation: fon 2s 1 ease-out;
}
.about__clip {
  clip-path: url(#svgAbout);
}

.works {
  background: url(../images/bg/consist_wave_top.svg) repeat-x center bottom;
}
.works__first {
  display: flex;
  flex-direction: column;
  padding-bottom: 150px;
}
.works__heading {
  text-align: center;
  margin: 0;
  margin-bottom: 10px;
}
.works__subheading {
  text-align: center;
  margin: 0;
  margin-bottom: 36px;
}
.works__limit {
  display: flex;
}
.works__photos {
  display: flex;
  flex-direction: column;
}
.works__photos-tabs {
  margin-left: auto;
  margin-right: 20px;
  display: inline-flex;
  justify-content: space-between;
  margin-bottom: 20px;
  background-color: #FFEEF3;
  border-radius: 30px;
  text-align: right;
}
.works__photos-all {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  margin-right: -10px;
}
.works__type {
  margin: 0 15px;
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  color: #722C5B;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 3;
}
.works__type:first-child {
  margin-left: 20px;
}
.works__type:last-child {
  margin-right: 20px;
}
.works__type_active {
  color: #ffffff;
  height: 60px;
  width: 90%;
  background-color: #F3ADC8;
  padding: 14px 25px;
  border-radius: 30px;
  margin: 0;
  transition: all 200ms ease-out;
}
.works__type_active:first-child {
  margin-left: 0;
}
.works__type_active:last-child {
  margin-right: 0;
}
.works__photo {
  position: relative;
  width: 31.5%;
  margin-right: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  border: 1px solid #FFEEF3;
  overflow: hidden;
  cursor: pointer;
  outline: none;
  box-shadow: 0px 3px 15px rgba(94, 189, 228, 0.3);
}
.works__photo-info {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 2;
  background: rgba(255, 255, 255, 0.85);
  width: 100%;
  padding: 30px 45px;
  transition: opacity 0.3s ease-in-out;
}
.works__title {
  font-family: "Exo2-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #722C5B;
  margin: 0;
  margin-bottom: 16px;
}
.works__description {
  font-weight: 300;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  color: #938B88;
  margin: 0;
}
.works__hide {
  display: none;
}

.works__photo-link:hover .works__photo-info {
  opacity: 1;
}

.consist {
  background-color: #FFEEF3;
  background-image: url(../images/bg/consist_wave_bottom.svg), url(../images/bg/stars.svg);
  background-repeat: repeat-x, repeat;
  background-position: center bottom, 0 0;
}
.consist__container {
  display: flex;
  align-items: start;
  padding-top: 63px;
  padding-bottom: 60px;
}
.consist__first {
  margin-right: 20px;
  margin-top: 20px;
}
.consist__title {
  margin: 0;
  margin-bottom: 20px;
}
.consist__limit {
  display: flex;
}
.consist__caption {
  margin-left: auto;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  background-color: #ffffff;
  border-radius: 30px;
  text-align: right;
  height: 60px;
}
.consist__type {
  margin: 0 15px;
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  color: #722C5B;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 3;
}
.consist__type:first-child {
  margin-left: 20px;
}
.consist__type:last-child {
  margin-right: 20px;
}
.consist__type_active {
  display: flex;
  align-items: center;
  color: #ffffff;
  margin: 0;
  padding-right: 25px;
  padding-left: 25px;
  height: 100%;
  background-color: #F3ADC8;
  border-radius: 30px;
  transition: all 200ms ease-out;
}
.consist__type_active:first-child {
  margin-left: 0;
}
.consist__type_active:last-child {
  margin-right: 0;
}
.consist__info {
  display: none;
}
.consist__info-active {
  display: block;
}
.consist__text {
  margin: 0;
  margin-bottom: 10px;
}
.consist__second {
  position: relative;
  width: 550px;
}
.consist__image-wrapper::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 0px;
  width: 550px;
  height: 434px;
  background: url("../images/blobs/consist-wrp.svg") no-repeat center center;
  animation: consist-pulse 4.5s infinite linear;
}
.consist__image-fon {
  width: 550px;
  height: 405px;
  object-fit: cover;
}
.consist__clip {
  clip-path: url(#svgConsist);
}
.consist__button {
  position: absolute;
  top: 199px;
  left: 103px;
  z-index: 1;
  outline: none;
}
.consist__button::before {
  position: absolute;
  content: "";
  z-index: -1;
  top: -32px;
  left: -32px;
  height: 128px;
  width: 128px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  animation: consist-play 2s infinite linear;
}
.consist__button::after {
  position: absolute;
  content: "";
  z-index: -1;
  top: -64px;
  left: -64px;
  height: 192px;
  width: 192px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  animation: consist-play 2s 1s infinite linear;
}

.steps {
  background-image: url(../images/bg/consist_wave_bottom.svg), url(../images/bg/faq_wave_top.svg);
  background-repeat: repeat-x, repeat-x;
  background-position: center top, center bottom;
}
.steps__first {
  padding-top: 182px;
  padding-bottom: 162px;
}
.steps__heading {
  margin: 0;
  margin-bottom: 10px;
  text-align: center;
}
.steps__subheading {
  margin: 0;
  margin-bottom: 50px;
}
.steps__block {
  display: flex;
  justify-content: space-around;
  align-items: start;
}
.steps__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 293px;
}
.steps__info {
  display: flex;
  flex-direction: column;
}
.steps__wrp {
  margin-bottom: 20px;
}
.steps__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 36px;
  line-height: 48px;
  text-align: center;
  color: #722C5B;
}
.steps__text {
  margin: 0;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
}

.faq {
  background: #F3FAFF url(../images/bg/stars.svg) repeat 0 0;
}
.faq__first {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 92px;
  padding-bottom: 118px;
}
.faq__heading {
  margin: 0;
  margin-bottom: 10px;
}
.faq__subheading {
  margin: 0;
  margin-bottom: 50px;
}
.faq__items {
  width: 820px;
}
.faq__item {
  margin-bottom: 20px;
  padding: 15px 30px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgba(94, 189, 228, 0.05);
  transition: all 0.3s linear;
}
.faq__title {
  position: relative;
  margin: 0;
  padding-top: 7px;
  padding-bottom: 7px;
  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  color: #722C5B;
  cursor: pointer;
}
.faq__title::before {
  position: absolute;
  content: "";
  top: 10px;
  right: 3px;
  transition: all 0.5s ease-out;
  transform: rotate(0deg);
  background: url(../images/icons/plus.svg) no-repeat center center;
  height: 34px;
  width: 34px;
  border: 2px solid #F3ADC8;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}
.faq__text {
  margin: 0;
  visibility: hidden;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px;
  color: #938B88;
  height: 0px;
  transition: height 0.3s linear, opacity 0.3s linear;
  opacity: 0;
}
.faq__item.faq__active {
  margin-bottom: 40px;
  box-shadow: 0px 20px 25px -3px rgba(94, 189, 228, 0.17);
}
.faq__item.faq__active .faq__text {
  margin-bottom: 15px;
  visibility: visible;
  height: 100%;
  opacity: 1;
}
.faq__item.faq__active .faq__title {
  padding-top: 15px;
}
.faq__item.faq__active .faq__title::before {
  transition: all 0.5s ease-out;
  transform: rotate(90deg);
  background: url(../images/icons/arrow-right.svg) no-repeat center center;
  border: 2px solid transparent;
}

.price {
  background-image: url(../images/bg/promo_wave.svg), url(../images/bg/consist_wave_top.svg);
  background-repeat: repeat-x, repeat-x;
  background-position: center -1px, center bottom;
}
.price__first {
  padding: 120px 0;
}
.price__heading {
  margin: 0;
  margin-bottom: 10px;
  text-align: center;
}
.price__subheading {
  margin: 0;
  margin-bottom: 50px;
}
.price__block {
  display: flex;
  justify-content: space-around;
}
.price__card {
  width: 320px;
}
.price__wrapper {
  position: relative;
  width: 300px;
  height: 300px;
  margin-bottom: 35px;
}
.price__wrapper::before {
  content: "";
  position: absolute;
  width: 400px;
  height: 400px;
  background: url("../images/blobs/price-wrp.svg") no-repeat center center;
  left: -40px;
  top: -40px;
  animation: price-pulse-1 2.5s infinite linear;
}
.price-first {
  clip-path: url(#svgPrice1);
}
.price-second {
  clip-path: url(#svgPrice2);
}
.price-third {
  clip-path: url(#svgPrice3);
}
.price__title {
  font-weight: normal;
  font-size: 36px;
  line-height: 48px;
  text-align: center;
  color: #722C5B;
  margin-bottom: 10px;
}
.price__text {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  color: #938B88;
}
.price__coast {
  display: block;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #722C5B;
}

.reviews {
  padding-top: 120px;
  padding-bottom: 120px;
  height: 630px;
  background: #FFEEF3 url(../images/bg/stars.svg) repeat 0 0;
}
.reviews-container {
  position: relative;
}
.reviews__heading {
  margin-bottom: 50px;
}
.reviews__tabs {
  display: flex;
  justify-content: center;
  top: 110px;
  left: 0;
  right: 0;
}
.reviews__tab {
  width: 505px;
  padding: 45px 50px 50px;
  background-color: #ffffff;
  box-shadow: 0px 7px 20px #F3ADC8;
  border-radius: 10px;
}
.reviews__tab:nth-child(1) {
  margin-right: 30px;
}
.reviews__profile {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
}
.reviews__photo {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 5px solid #F3ADC8;
  margin-right: 16px;
}
.reviews__name {
  font-family: "Nunito-Bold";
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #722C5B;
  margin-bottom: 5px;
}
.reviews__info {
  font-family: "Nunito-Light";
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #938B88;
}
.reviews__title {
  position: relative;
  font-style: italic;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #722C5B;
  margin: 0;
  margin-bottom: 20px;
  z-index: 1;
}
.reviews__title::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  background: url(../images/icons/quote.svg) no-repeat 0 0;
  width: 100px;
  height: 74px;
}
.reviews__text {
  font-family: "Nunito-Light";
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  color: #938B88;
  margin: 0;
  margin-bottom: 10px;
}
.reviews__text:last-child {
  margin-bottom: 0;
}
.reviews-slider {
  padding-bottom: 40px;
}
.reviews-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 2px solid #F3ADC8;
  fill: #F3ADC8;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  z-index: 50;
