.up {
  display: none;
  position: fixed;
  right: 40px;
  bottom: 40px;
  width: 30px;
  height: 30px;
  z-index: 1;
}
.up::after {
  content: "";
  position: absolute;
  top: -25px;
  left: -25px;
  width: 80px;
  height: 80px;
  background-color: #FFEEF3;
  border-radius: 50%;
  z-index: -1;
  animation: consist-play 2s infinite linear;
}
.up::before {
  position: absolute;
  content: "";
  z-index: -1;
  top: -25px;
  left: -25px;
  width: 80px;
  height: 80px;
  background-color: #FFEEF3;
  border-radius: 50%;
  animation: consist-play 3s infinite linear;
}
